.column-3{
    width: 75%;
    float: left;
}
.column-2{
    width: 50%;
    float: left;
}
.column-1{
    width: 25%;
    float: left;
}

button.clac-button{
    width: 100%;
    height: 100px;
    border: 2px solid gray;
    background: #cccacf;
    font-size: 32px;
    color: black;
}
input.result{
    height: 100px;
    width:auto;
    border: none;
    text-align: left;
    font-size: 40px;
    position: relative;
    max-width: 100vw;
    margin-left: 50px;
    float: left;
}
.float-last{
    position: absolute;
    left: 50px;
    z-index: 99;
    top: 10px;
    color: #aaa;
    font-size: 17px;
    float: left;
}
